.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  padding: 0 20px;
}

.header span {
  font-size: 18px;
  color: blue;
}

.header a {
  font-size: 18px;
  color: blue;
}

.btnLogout {
  padding: 6px 15px;
  border: 1px solid black;
  border-radius: 5px;
  background: rgba(0, 0, 255, 0.8);
  font-size: 14px;
  color: white;
}

.btnLogout:hover, .btnLogout:active {
  border: 2px solid rgba(0, 0, 255, 0.8);
  background: white;
  color: rgba(0, 0, 255, 0.8);
  cursor: pointer;
}

.btnLogout:active {
  background: rgba(0, 0, 255, 0.3);
}
